/* Large desktops and laptops */
@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

/* Landscape tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {
  .container {
    max-width: 900px;
  }
  .first-screen {
    &__content {
      padding: 50px;
    }
    &__title {
      margin-bottom: 30px;
    }
    &__subtitle {
      margin-bottom: 50px;
    }
    &__feature {
      margin-bottom: 30px;
    }
  }
  .feature {
    &__text {
      min-height: 70px;
    }
  }
  .brigade {
    &__row {
      padding: 0 50px;
    }
    &__item {
      flex-basis: 32%;
    }
  }
  .consultation-form {
    &__row {
      padding: 0 80px;
    }
  }
  .header {
    &__work-time {
      position: static;
      left: auto;
      transform: none;
    }
  }
}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .container {
    max-width: 750px;
  }
  .header {
    &__work-time {
      position: static;
      left: auto;
      transform: none;
    }
  }
  .first-screen {
    &__content {
      padding: 30px;
    }
    &__title {
      margin-bottom: 20px;
      font-size: 3rem;
    }
    &__subtitle {
      font-size: 2rem;
      margin-bottom: 30px;
    }
    &__feature {
      margin-bottom: 20px;
      padding-right: 20px;
    }
  }
  .features {
    &__row {
      padding: 0;
    }
    &__item {
      flex-basis: calc(25% - 15px);
    }
  }
  .feature {
    &__text {
      min-height: 70px;
    }
  }
  .find-detail {
    &__form {
      margin-bottom: 60px;
      & > form {
        display: flex;
        justify-content: space-between;
      }
    }
    &__input,
    &__btn {
      flex-basis: 48%;
    }
    &__input {
      margin-bottom: 20px;
    }
    &__btn {
      & > input {
        padding: 28px 0;
      }
      margin: auto;
    }
  }
  .warranty {
    &__row {
      padding-left: 0px;
      padding-right: 0px;
    }
  }
  .conditions {
    &__row {
      justify-content: space-between;
    }
    &__item {
      flex-basis: calc(33% - 50px);
    }
  }
  .brigade {
    &__row {
      padding: 0;
    }
    &__item {
      flex-basis: 32%;
    }
  }
  .consultation-form {
    &__row {
      padding: 0;
    }
  }
}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .container {
    max-width: 450px;
  }
  .header {
    &__inner {
      flex-direction: column;
      align-items: center;
    }
    &__logo {
      margin-bottom: 10px;
    }
    &__work-time {
      position: static;
      left: auto;
      transform: none;
      margin-bottom: 10px;
    }
    &__call {
      flex-direction: column;
    }
    &__phones {
      margin-bottom: 10px;
    }
    &__call-btn {
      margin-left: 0;
    }
  }
  .first-screen {
    &__content {
      padding: 30px;
      width: 60%;
      // position: relative;
      position: absolute;
      left: 0;
      // left: -100px;
    }
    &__title {
      margin-bottom: 20px;
      font-size: 3rem;
    }
    &__subtitle {
      font-size: 2rem;
      margin-bottom: 30px;
    }
    &__feature {
      margin-bottom: 20px;
      padding-right: 20px;
    }
  }
  .get-price {
    background-position: 45% center;
    &__title {
      text-align: center;
    }
  }
  .get-price-form {
    & > form {
      justify-content: center;
    }
    &__input,
    &__btn,
    &__text {
      flex-basis: 90%;
    }
    &__text {
      margin-bottom: 30px;
      font-size: 1.7rem;
    }
  }
  .features {
    &__row {
      padding: 0;
    }
    &__item {
      flex-basis: 48%;
      margin-bottom: 30px;
    }
  }
  .feature {
    &__text {
      min-height: 70px;
    }
  }
  .find-detail {
    &__form {
      margin-bottom: 60px;
      & > form {
        display: flex;
        justify-content: center;
      }
    }
    &__input,
    &__btn {
      flex-basis: 90%;
    }
    &__input {
      margin-bottom: 20px;
    }
    &__btn {
      & > input {
        padding: 28px 0;
      }
    }
  }
  .warranty {
    &__row {
      padding-left: 0px;
      padding-right: 0px;
      justify-content: center;
    }
    &__item {
      flex-basis: 70%;
      margin-bottom: 30px;
    }
  }
  .bonus {
    &__item {
      flex-basis: 45%;
      margin-bottom: 50px;
    }
  }
  .questions {
    &__list-wrapper {
      flex-basis: 100%;
    }
    &__photo {
      display: none;
    }
  }
  .conditions {
    &__row {
      flex-direction: column;
      justify-content: space-between;
    }
    &__item {
      margin-bottom: 30px;
    }
  }
  .brigade {
    &__row {
      padding: 0;
      justify-content: center;
    }
    &__item {
      flex-basis: 70%;
      margin-bottom: 30px;
    }
  }
  .consultation-form {
    &__row {
      padding: 0;
      justify-content: center;
    }
    &__input {
      margin-bottom: 30px;
    }
    &__input,
    &__btn {
      flex-basis: 90%;
    }
    &__btn input {
      padding-top: 25px;
      padding-bottom: 25px;
    }
  }
  .footer {
    &__inner {
      flex-direction: column;
    }
    &__logo {
      margin-bottom: 10px;
    }
    &__madeby {
      position: static;
      transform: none;
      flex-wrap: wrap;
      justify-content: center;
      margin-bottom: 10px;
    }
    &__call span {
      margin-bottom: 20px;
    }
  }
}

/* Portrait phones and smaller */
@media (max-width: 480px) {
  body {
    font-size: 1.6rem;
  }
  .container {
    max-width: 320px;
  }
  .section-header {
    &__title {
      font-size: 3rem;
      font-weight: 900;
      margin-bottom: 30px;
    }
    &__subtitle {
      font-size: 1.7rem;
      font-weight: 400;
    }
  }
  .first-screen {
    &__content {
      width: 100%;
      position: static;
    }
    &__title {
      font-size: 2.5rem;
      text-align: center;
    }
    &__subtitle {
      text-align: center;
    }
    &__features {
  
    }
    &__feature {
      padding-right: 0;
    }
  }
  .bonus {
    &__item {
      flex-basis: 100%;
    }
  }
}

